<template>
  <div class="synSelect">
    <!--  -->
    <el-row type="flex">
      <el-col :span="4" class="yxxt-col" v-if="options.year">
        <label> </label>
        <el-select v-model="option.year" placeholder="" class="yxxt-select" size="mini"  @change="changeYear">
          <el-option v-for="item in options.year" :key="item" :label="item" :value="item" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col" v-if="options.title">
        <label> </label>
        <el-select v-model="option.title" placeholder="" class="yxxt-select" size="mini"  @change="changSys">
          <el-option v-for="item in options.title" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col">
        <label> </label>
        <el-select v-model="option.type" placeholder="" class="yxxt-select" size="mini">
          <el-option v-for="item in options.type" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col :span="4" class="yxxt-col" v-if="options.school">
        <label> </label>
        <el-select v-model="option.school" placeholder="" class="yxxt-select" size="mini"  @change="changeSchool">
          <el-option v-for="item in options.school" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col" v-if="options.majon">
        <label> </label>
        <el-select v-model="option.majon" placeholder="" class="yxxt-select" size="mini"  @change="changeMajon">
          <el-option v-for="item in options.majon" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col" v-if="options.class">
        <label> </label>
        <el-select v-model="option.classes" placeholder="" class="yxxt-select" size="mini">
          <el-option v-for="item in options.class" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col"  v-if="options.soucre">
        <label> </label>
        <el-select v-model="option.soucre" placeholder="" class="yxxt-select" size="mini">
          <el-option v-for="item in options.soucre" :key="item" :label="item" :value="item" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col :span="4" class="yxxt-col" v-if="options.comb">
        <label> </label>
        <el-select v-model="option.combtype" placeholder="" class="yxxt-select" size="mini">
          <el-option v-for="item in options.comb" :key="item.id" :label="item.name" :value="item.id" class="yxxt-option">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="4" class="yxxt-col">
        <span class="search" @click="search"></span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {fetchCondition, fetchMajon, fetchClass, fetchLink, fetchYear } from 'oa/api/statis/synthesis'
  import { mapMutations } from 'vuex'
  const OK_CODE = '200'
  export default {
    data() {
      return {
        options: {},
        option:{
          year: '', // 
          title: '', // 
          type: '', // 
          majon: '', // 
          school: '', // 
          classes: '', // 
          soucre: '', // 
          combtype: '' // 
        }
      }
    },
    methods: {
      // 
      _combtitle() {
        let loading = this.loading() // ...
        fetchCondition().then(res => {
          loading.close()
          res = res.data
          if (res.state === OK_CODE) {
            this.options = res.data
            // 
            this.option.year = this.options.year.length > 0 ? this.options.year[0] : ''
            this.option.title = this.options.title.length > 0 ? this.options.title[0].id : ''
            this.option.majon = this.options.majon.length > 0 ? this.options.majon[0].id : ''
            this.option.school = this.options.school.length > 0 ? this.options.school[0].id : ''
            this.option.classes = this.options.class.length > 0 ? this.options.class[0].id : ''
            this.option.soucre = this.options.soucre.length > 0 ? this.options.soucre[0] : ''
            this.option.combtype = this.options.comb.length > 0 ? this.options.comb[0].id : ''
            this.option.type = this.options.type.length > 0 ? this.options.type[0].id : ''
            this.$emit('search', this.option)
          }
        }).catch(_ => {
          loading.close()
          this.MessageError()
        })
      },
      // 
      changeSchool() {
        // 
        let loading = this.loading()
        fetchMajon(this.option.school).then(res => {
          loading.close()
          res = res.data
          if (res.state === OK_CODE) {
            this.options.majon = res.data.majon // 
            this.options.class = res.data.class // 
            this.option.majon = this.options.majon.length > 0 ? this.options.majon[0].id : ''
            this.option.classes = this.options.class.length > 0 ? this.options.class[0].id :''
          }
        }).catch(_ => {
          loading.close()
          this.MessageError()
        })
      },
      // 
      changeMajon() {
        // 
        let loading = this.loading()
        fetchClass(this.option.school ,this.option.majon).then(res => {
          loading.close()
          res = res.data
          if (res.state === OK_CODE) {
            this.options.class = res.data.class // 
            this.option.classes = this.options.class.length > 0 ? this.options.class[0].id : ''
          }
        }).catch(_ => {
          loading.close()
          this.MessageError()
        })
      },
      // 
      changSys() {
        let loading = this.loading()
        fetchLink(this.option.title).then(res => {
          res = res.data
          loading.close()
          if (res.state === OK_CODE) {
            this.options.type = res.data.process
            this.option.type = this.options.type.length > 0 ? this.options.type[0].id : ''
          }
        }).catch(_ => {
          loading.close()
          this.MessageError()
        })
      },
      // 
      changeYear() {
        let loading = this.loading()
        fetchYear(this.option.year).then(res => {
          loading.close()
          res = res.data
          if (res.state === OK_CODE) {
            this.options.title = res.data.title
            this.option.title = this.options.title.length > 0 ? this.options.title[0].id : ''
          } else {
            this.MessageError(res.message)
          }
        }).catch(() => {
          loading.close()
          this.MessageError()
        })
      },
      // 
      search() {
        this.$emit('search', this.option)
      },
      ...mapMutations({
        '_setIndex': 'SET_INDEX'
      })
    },
    activated () {
      this._setIndex('2')
      this._combtitle()
    }
  }
</script>

<style lang="stylus" scoped>
@import '~oa/style/variable'
  .synSelect
    .yxxt-col
      display flex
      margin-left 10px
      margin-top 5px
      label
        width 70px
        line-height 30px
      .yxxt-select
        flex 1
        // &>>>input 
        //   height $input-heigt
        //   line-height $input-heigt
      .search
        display inline-block
        height 28px
        padding 0 15px
        box-sizing border-box
        line-height 28px
        border 1px solid $color-theme
        border-radius 3px
        color $color-theme
        cursor pointer
</style>



